@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/roboto-condensed-v19-latin-regular.woff2") format("woff2"), url("../fonts/roboto-condensed-v19-latin-regular.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Roboto Condensed";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/roboto-condensed-v19-latin-700.woff2") format("woff2"), url("../fonts/roboto-condensed-v19-latin-700.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/roboto-v20-latin-regular.woff2") format("woff2"), url("../fonts/roboto-v20-latin-regular.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/roboto-v20-latin-500.woff2") format("woff2"), url("../fonts/roboto-v20-latin-500.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: "PT Mono";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/pt-mono-v8-latin-regular.woff2") format("woff2"), url("../fonts/pt-mono-v8-latin-regular.woff") format("woff");
  font-display: swap; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

html {
  box-sizing: border-box; }

body {
  font-family: "PT Mono", "Times New Roman", serif;
  font-size: 16px;
  background-color: #ffffff;
  color: #666666;
  margin: auto;
  min-width: 320px; }

h2 {
  font-family: "Roboto Condensed", "Arial", sans-serif; }

a {
  text-decoration: none; }

li {
  list-style: none; }

.description {
  display: flex;
  justify-content: center; }

.description__wrapper {
  max-width: 1170px;
  display: flex;
  justify-content: center;
  padding-bottom: 96px; }

.description__text h2 {
  font-size: 36px;
  line-height: 50.63px;
  margin: 0;
  padding-bottom: 18px; }

.description__text p {
  line-height: 21.5px;
  margin: 0;
  padding-bottom: 15px; }

.description__text a {
  color: #0ab2f0; }

.button {
  font-size: 14px;
  line-height: 16.41px;
  font-weight: 700;
  text-transform: uppercase;
  border: 1px solid;
  border-color: inherit;
  border-radius: 5%;
  padding: 14px 23px;
  margin-top: 15px;
  display: inline-block; }

.advantages {
  display: flex;
  justify-content: center; }

.advantages__wrapper {
  padding-top: 109px;
  padding-bottom: 109px; }

.advantages__list {
  display: flex;
  flex-wrap: wrap;
  display: grid;
  grid-template-columns: repeat(auto-fill, 570px);
  grid-column-gap: 30px;
  margin: 0;
  padding: 0;
  justify-content: center;
  max-width: 1170px; }

.advantages__card {
  padding: 40px 45px;
  border-left: 2px solid #0ab2f0;
  margin-bottom: 30px; }
  .advantages__card h2 {
    font-size: 24px;
    line-height: 33.75px;
    padding-top: 23px;
    padding-bottom: 15px;
    margin: 0;
    color: #0ab2f0; }
  .advantages__card p {
    line-height: 21.5px;
    margin: 0; }
